import global from '../../mixins/global.js';
export default {
	mixins: [global],
	data() {
		return {
			categoryData: [],
			tabbar: [
				'推荐分类',
				'进口超市',
				'国际名牌',
				'奢侈品',
				'海囤全球',
				'男装',
				'女装',
				'男鞋',
				'女鞋',
				'钟表珠宝',
				'手机数码',
				'电脑办公',
				'家用电器',
				'玩具乐器',
				'运动户外',
				'宠物生活',
				'特产馆'
			],
			height: 0, //scroll-view高度
			currentTab: 0, //预设当前项的值
			scrollTop: 0 //tab标题的滚动条位置
		};
	},
  onShow(){
    uni.showTabBar()
  },
	onLoad(options) {
		uni.getSystemInfo({
			success: res => {
				let header = 92;



				this.height = res.windowHeight - uni.upx2px(header);
			}
		});
	},
	mounted() {
		this.$http({
			aurl: 'getCategory',
			success: res => {
				console.log(res);
				let dataArr = res.data.result;
				if (dataArr.length > 0) {
					this.categoryData = dataArr;
					console.log(this.categoryData);
				}
			},
			fail: err => {
				console.log(res);
				uni.showToast({
					title:'请求超时，请稍后再重试',
					icon:'none'
				})
			}
		});
	},
	methods: {
		// 点击标题切换当前页时改变样式
		swichNav(e) {
			let cur = e.currentTarget.dataset.current;
			if (this.currentTab == cur) {
				return false;
			} else {
				this.currentTab = cur;
				this.checkCor();
			}
		},
		//判断当前滚动超过一屏时，设置tab标题滚动条。
		checkCor() {
			let that = this;
			//这里计算按照实际情况进行修改，动态数据要进行动态分析
			//思路：窗体高度/单个分类高度 200rpx 转px计算 =>得到一屏幕所显示的个数，结合后台传回分类总数进行计算
			//数据很多可以多次if判断然后进行滚动距离计算即可
			if (that.currentTab > 7) {
				that.scrollTop = 500;
			} else {
				that.scrollTop = 0;
			}
		},
		productList(e) {
			let gc_id  = e.currentTarget.dataset.gcid
			uni.navigateTo({
				url: `/pages/goods/lists?gc_id=${gc_id}`
			});
		},
		search() {
			uni.navigateTo({
				url: '/pages/goods/search'
			});
		}
	}
};
